.btn-scroll {
  display: inline-block;
  padding: 10px 20px;
  border-radius: 3px;
  border: 1px solid #fff;
  background-color: inherit;
  color: #fff;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  margin-bottom: 75px;
}

@media (max-width: 500px) {
  .btn-scroll {
    margin-bottom: 27px;
  }
}
